.sidebar{
    display: flex;
    flex-direction: column;
    flex: 0.35;
}
.sidebar_header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    border-right: 1px solid lightgray;
}

.sidebar_headerRight{
    width: 100px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 7vw;
}
.sidebar_search{
    display: flex;
    align-items: center;
    background-color: #f6f6f6;
    height: 39px;
    padding: 10px;
}
.sidebar_searchContainer{
    display: flex;
    align-items: center;
    background-color: white;
    width: 100%;
    height: 35px;
    border-radius: 20px;
}
.sidebar_searchContainer>.MuiSvgIcon-root{
    color: gray;
    padding: 10px;
}
.sidebar_searchContainer>input{
    border: none;
    outline-width: 0;
    margin-left: 10px;
}
.sidebar_charts{
    flex: 1;
    background-color: white;
    overflow: scroll;
}